body {
    margin: 0;
    padding: 0;
}

.home .memories img, .memory-image {
    width: 150px;
    height: 168px;
    position: absolute;
    top: 0px;
    left: 0px;
}

.single-memory-image {
    width: auto;
    height: auto;
    position: absolute;
}

.home .memories {
    overflow: hidden;
    padding-left: 80px;
}

.fontWeight, .quote, h1, h2, h3, h4, h5, h6 {
    font-weight: 100;
}

.pull-right {
    float: right;
}

.cursor {
    cursor: pointer;
}
